.content {
  margin: 100px 0;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.calculatingContent {
  padding: 0 60px;
}

.text {
  font-size: 15px;
  font-weight: 100;
  color: #ffe5e5;
  font-family: "DotGothic16", Arial, Helvetica, sans-serif;
}

.text + .text {
  margin-top: 20px;
}

.finanseItem {
  padding: 0 60px;
  margin: 0;
  margin-top: 95px;

  @media (max-width: 770px) {
    padding: 0 10px;
    margin-top: 95px;
  }
}

.alertTitle {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
}
